@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body{margin:0;font-family:'Roboto Mono', monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden}code{font-family:'Roboto Mono', monospace}

.App{height:100vh;padding:10vh 0;box-sizing:border-box;background-color:#434343}

.safe-container{position:relative;width:250px;margin:0 auto;padding:40px 50px;background:#7d7d7f;border-radius:5px;box-shadow:0px 0px 2px 1px rgba(23,23,23,0.7)}.safe-container .safe-number{position:absolute;right:10px;bottom:10px;margin-top:20px;font-size:12px;color:#434343}

.keyboard-container{display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center}

.safe-key{width:30%;padding:15px;margin-bottom:10px;outline:none;font-size:30px;background-color:#63636e;color:#f3f3f3;border:none;border-radius:5px;box-shadow:0px 0px 2px 1px rgba(23,23,23,0.7);transition:background-color 300ms ease;cursor:pointer}.safe-key:hover{background-color:#434343}

.screen-container{display:flex;flex-direction:column;justify-content:space-between;height:70px;margin-bottom:20px;border-radius:5px;padding:5px}.screen-container.inactive{background-color:#47b2b2}.screen-container.active{background-color:#7fffff}.screen-container .big-screen{text-align:right;font-size:32px}

